
html, body {
  overflow: hidden;
  height: 100%;
  padding: 0;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
  background: rgba(0, 0, 0, 0) radial-gradient(rgb(240, 240, 240), rgb(190, 190, 190)) repeat scroll 0 0;
}
.impress-step {
  text-align: center;
  cursor: pointer;
}
.impress-step img {
  width: 100px;
  height: 100px;
}
.impress-step.active {
  cursor: auto;
}



